import angular from 'angular'

export default angular
  .module('NotificationsControllerModule', [])
  .controller('NotificationsController', function ($http, $scope) {
    $scope.saveNotifications = null

    $scope.updateNotifications = function () {
      delete $scope.error
      delete $scope.message
      $scope.saveNotifications().then(
        () => {
          $scope.message = 'Saved changes successfully'
        },
        () => {
          $scope.message = 'Unable to update notifications'
        }
      )
    }
  })
